"use client";
import React from 'react';
import { useToast } from './ToastContext';

export default function ToastView() {
  const { toasts, removeToast } = useToast();

  if (!toasts.length) return null;

  return (
    <div className="fixed right-4 bottom-4 z-50 flex flex-col gap-3">
      {toasts.map((t) => (
        <div key={t.id} className={`toast-card max-w-sm ${t.type === 'success' ? 'toast-success' : t.type === 'error' ? 'toast-error' : 'toast-info'}`}>
          <div className="flex-1 text-sm">{t.message}</div>
          <div className="flex items-center gap-2">
            <button onClick={() => removeToast(t.id)} className="text-xs text-slate-500">Dismiss</button>
          </div>
        </div>
      ))}
    </div>
  );
}
