'use client';

import { useState, ReactNode, useEffect } from 'react';
import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { ToastProvider } from './ToastContext';
import ToastView from './ToastView';

interface LayoutProps {
  children: ReactNode;
}

export default function Layout({ children }: { children: ReactNode }) {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const [user, setUser] = useState<any>(null);
  const [mounted, setMounted] = useState(false);
  const pathname = usePathname();
  const router = useRouter();

  useEffect(() => {
    try {
      const userStr = localStorage.getItem("user");
      if (userStr) setUser(JSON.parse(userStr));
    } catch (e) {
      // ignore
    }
    setMounted(true);
  }, []);

  const isActive = (path: string) => pathname === path;

  const handleLogout = () => {
    try {
      localStorage.removeItem("token");
      localStorage.removeItem("user");
      localStorage.removeItem("userRole");
    } catch (e) {
      // ignore
    }
    setUser(null);
    setMobileMenuOpen(false);
    router.push("/");
  };

  return (
    <ToastProvider>
      <div className="flex flex-col min-h-screen bg-white">
        <header className="site-header sticky top-0 z-50 bg-white/70 backdrop-blur-sm border-b">
          <div className="site-container h-16 flex items-center justify-between relative">
            <Link href="/" className="flex items-center gap-3">
              <div className="w-10 h-10 rounded-lg bg-gradient-to-br from-indigo-600 to-blue-600 flex items-center justify-center text-white font-bold text-lg">üè•</div>
              <span className="font-bold text-xl text-slate-900 hidden sm:inline">ClinicHub</span>
            </Link>

            <nav className="hidden lg:flex items-center gap-6 absolute left-1/2 transform -translate-x-1/2">
              <Link href="/" className={`nav-link ${isActive("/") ? "text-indigo-600" : "text-slate-700 hover:text-indigo-600"}`}>
                Home
              </Link>
              <Link href="/clinics" className={`nav-link ${isActive("/clinics") ? "text-indigo-600" : "text-slate-700 hover:text-indigo-600"}`}>
                Clinics
              </Link>
              <Link href="/doctors" className={`nav-link ${isActive("/doctors") ? "text-indigo-600" : "text-slate-700 hover:text-indigo-600"}`}>
                Doctors
              </Link>
            </nav>

            <div className="flex items-center gap-4">
              {mounted && user ? (
                <div className="flex items-center gap-3">
                  <div className="hidden sm:block text-sm">
                    <div className="font-semibold text-slate-900">{user.full_name?.split(" ")[0]}</div>
                  </div>
                  <button onClick={handleLogout} className="hidden sm:inline-block px-4 py-2 rounded-lg bg-slate-100 hover:bg-slate-200 text-slate-700 font-semibold">Logout</button>
                </div>
              ) : mounted ? (
                <div className="hidden sm:flex items-center gap-2">
                  <Link href="/login"><button className="px-4 py-2 rounded-lg text-slate-700 font-semibold hover:bg-slate-100">Login</button></Link>
                  <Link href="/register"><button className="px-4 py-2 rounded-lg bg-indigo-600 text-white font-semibold hover:bg-indigo-700">Sign Up</button></Link>
                </div>
              ) : null}

              <button onClick={() => setMobileMenuOpen((s) => !s)} className="lg:hidden p-2 rounded-lg hover:bg-slate-100">{mobileMenuOpen ? "‚úï" : "‚ò∞"}</button>
            </div>
          </div>

          {mobileMenuOpen && (
            <div className="lg:hidden absolute inset-x-0 top-16 bg-white border-b shadow-lg">
              <nav className="site-container py-4 flex flex-col gap-2">
                <Link href="/" onClick={() => setMobileMenuOpen(false)} className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium">Home</Link>
                <Link href="/clinics" onClick={() => setMobileMenuOpen(false)} className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium">Clinics</Link>
                <Link href="/doctors" onClick={() => setMobileMenuOpen(false)} className="px-4 py-2 rounded-lg hover:bg-slate-100 font-medium">Doctors</Link>
                <div className="border-t border-slate-200 pt-3 mt-3">
                  {mounted && user ? (
                    <button onClick={handleLogout} className="w-full px-4 py-2 rounded-lg bg-red-50 text-red-700 font-semibold">Logout</button>
                  ) : mounted ? (
                    <div className="flex gap-2">
                      <Link href="/login" className="flex-1"><button onClick={() => setMobileMenuOpen(false)} className="w-full px-4 py-2 rounded-lg bg-slate-100">Login</button></Link>
                      <Link href="/register" className="flex-1"><button onClick={() => setMobileMenuOpen(false)} className="w-full px-4 py-2 rounded-lg bg-indigo-600 text-white">Sign Up</button></Link>
                    </div>
                  ) : null}
                </div>
              </nav>
            </div>
          )}
        </header>

        <main className="flex-1">{children}</main>

        <footer className="site-footer bg-slate-950 text-slate-50 border-t">
          <div className="site-container py-12">
            <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 text-sm text-slate-400">
              <div>¬© 2025 ClinicHub. All rights reserved.</div>
              <div>Made with ‚ù§Ô∏è for better healthcare access</div>
            </div>
          </div>
        </footer>

        <ToastView />
      </div>
    </ToastProvider>
  );
}
